<template>
	<div class="booking-container">
		<div class="booking-block booking-box room-box">
      <div class="room-pic">
        <img :src="pic.src" alt="">
      </div>
      <div class="room-info">
        <div class="room-name">{{roomName}}</div> 
        <div class="room-layout">{{roomArea}}m | {{houseType}} | {{decorationDegree}}</div> 
        <div class="room-price">{{price}}/</div> 
        <div class="room-address">{{address}}</div>
      </div>
		</div>
		<div class="booking-block tips"></div>
		<div class="booking-block">
			<div class="booking-box input-group">
				<label for=""></label>
        <input type="text" placeholder="" v-model="name">
			</div>
			<div class="booking-box input-group">
        <label for=""></label>
        <input type="tel" placeholder="" v-model="phone" maxlength="11">
			</div>
		</div>
		<div class="booking-block booking-box input-group">
			<label for=""></label>
      <input onfocus='this.blur();' @click="showPicker = true" type="text" placeholder="" v-model="selectedTime" readonly="readonly">
      <span class="icon"></span>
		</div>
    <div class="booking-block booking-box remarks">
      <label for=""></label>
      <textarea name="" id="" cols="30" rows="3" placeholder="" v-model="remark"></textarea>
    </div>
    <div class="order-btn">
      <button class="btn btn-primary" @click="checkForm">
        
      </button>
    </div>
    <mt-popup
      class="mj-picker"
      v-model="showPicker"
      position="bottom">
      <mt-picker 
        :slots="slots" 
        @change="onValuesChange"
        :visibleItemCount="3"
        :showToolbar="true"
        :value-key="'text'">
        <div class="mj-picker-head">
          <span class="fl" @click="showPicker = false"></span>
          
          <span class="fr" @click="setTime"></span>
        </div>
      </mt-picker>
    </mt-popup>
    <mt-popup
      class="checkCode-modal"
      v-model="showCheckCode"
      position="center"
      zIndex="500">
      <div class="checkCode-container">
        <div class="head"></div>
        <div class="active phone">{{phone}}</div>
        <mt-field 
          v-model="captcha" 
          placeholder=""
          :attr="{ maxlength: 6 }">
          <div @click="getCheckCode" :class="[sendCodeCount == 0 ? 'active' : '']">{{codeText}}</div>
        </mt-field>
        <p class="tips"></p>
        <span class="active" @click="showContract = true"></span>
      </div>
      <div class="checkCode-confirm active" @click="sendOrder"></div>
    </mt-popup>
    <mt-popup
      class="contract-modal"
      v-model="showContract"
      position="center">
      <div class="head">
        
        <span class="close-btn" @click="showContract = false"></span>
      </div>
      <div class="contract-container">
        <p></p>
        <h4 style="font-weight:700;">1.</h4>
        <p></p>
        <p>()</p>
        <p></p>
        <h4 style="font-weight:700;">2.</h4>
        <p></p>
        <p></p>
        <h4 style="font-weight:700;">3.</h4>
        <p></p>
        <p>
        1
        2) 
        3)</p>
        <h4 style="font-weight:700;">4.</h4>
        <p>/</p>
        <p>30/</p>
        <h4 style="font-weight:700;">5.</h4>
        <p></p>
        <p></p>
        <p></p>
        <p>1) </p>
        <p>2) </p>
        <p>3) </p>
        <h4 style="font-weight:700;">6.</h4>
        <p></p>
        <h4 style="font-weight:700;">7.</h4>
        <p></p>
        <p>1</p>
        <p>2</p>
        <p>3</p>
        <p>4</p>
        <p>5</p>
        <p>12</p>
        <h4 style="font-weight:700;">8.</h4>
        <p></p>
        <p></p></p>
        <p></p>
        <h4 style="font-weight:700;">9.</h4>
        <p>()</p>
        <p>1</p>
        <p>2</p>
        <p>3</p>
        <p>4</p>
        <p></p>
        <h4 style="font-weight:700;">10.</h4>
        <p></p>
        <p></p>
        <h4 style="font-weight:700;">11.</h4>
        <p></p>
        <p></p>
        <h4 style="font-weight:700;">12.</h4>
        <p>(24)</p>
        <h4 style="font-weight:700;">13.</h4>
        <p></p>
        <h4 style="font-weight:700;">14.</h4>
        <p></p>
        <p></p>
        <h4 style="font-weight:700;">15.</h4>
        <p></p>
        <p></p>
        <p></p>
        <p></p>
        <p></p>
      </div>
    </mt-popup>
    <v-dialog class="order-msg" width="75%" @closed="jumpUrl" />
	</div>
</template>

<script>

import { Toast } from 'mint-ui'
const weekList = ['', '', '', '', '', '', ''];
export default {
  props: ['roomName', 'pic', 'price', 'address', 'houseType', 'decorationDegree', 'roomArea'],
  data() {
    return {
      showPicker: false,
      slots: [
        {
          flex: 1,
          values: [],
          className: 'slot1',
          textAlign: 'center'
        }, {
          flex: 1,
          values: [],
          className: 'slot2',
          textAlign: 'center'
        }, {
          flex: 1,
          values: [],
          className: 'slot3',
          textAlign: 'center'
        }
      ],
      pickHList: [],
      pickMiList: [],
      todayHList: [],
      todayMiList: [],
      pickerVal: '',
      selectedTime: '',
      remark: '',
      name: '',
      phone: '',
      showCheckCode: false,
      showContract: false,
      captcha: '',
      codeText: '',
      sendCodeCount: 0,
      isCD: false,
      orderSuccess: false
    }
  },
  methods: {
    onValuesChange(picker, values) {
      if(values[0]) {
        if(values[0].text.indexOf('') !== -1){
          picker.setSlotValues(1, this.todayHList);
          picker.setSlotValues(2, this.todayMiList);
        } else {
          picker.setSlotValues(1, this.pickHList);
          picker.setSlotValues(2, this.pickMiList);
        }

        if(values[1] && values[2]) {
          let time = picker.getValues();
          this.pickerVal = time[0].year + '/' + time[0].month + '/' + time[0].date + 
                              '(' + time[0].day + ') ' + time[1].hour + ':' + 
                              time[2].minute;
        }
      }
    },
    setTimeList() {
      let today = new Date();
      let todayY = today.getFullYear();
      let todayM = today.getMonth();
      let todayD = today.getDate();

      for (var i = 0; i < 7; i++) {
        let date = new Date(todayY, todayM, todayD + i);
        let curY = date.getFullYear();
        let curM = date.getMonth() + 1;
        let curD = date.getDate();
        let curDText = curD < 10 ? '0' + curD : curD;
        let curDay = date.getDay();
        this.slots[0].values.push({
          text: i == 0 ? '' + '(' + weekList[curDay] + ')' : curM + '' + curDText + '' + '(' + weekList[curDay] + ')',
          year: curY,
          month: curM < 10 ? '0' + curM : curM,
          date: curD < 10 ? '0' + curD : curD,
          day: weekList[curDay]
        });
      }
      for (let i = 8; i <= 21; i++) {
        this.pickHList.push({
          text: i + '',
          hour: i < 10 ? '0' + i : i
        });
      }
      this.pickMiList.push({
        text: '00',
        minute: '00'
      });
      this.pickMiList.push({
        text: '30',
        minute: '30'
      });

      let todayH = today.getHours();
      let todayMi = today.getMinutes();
      if(todayMi >= 30){
        this.todayHList = this.pickHList.slice(todayH - 7);
        this.todayMiList = this.pickMiList;
      } else {
        this.todayHList = this.pickHList.slice(todayH - 8);
        this.todayMiList = this.pickMiList.slice(1);
      }
    },
    setTime() {
      this.showPicker = false;
      this.selectedTime = this.pickerVal;
    },
    checkForm() {
      if(this.name == ''){
        Toast({
          message: '',
          duration: 3000
        });
        return ;
      }
      if(this.name.length > 12 || this.name.length < 2){
        Toast({
          message: '2~12',
          duration: 3000
        });
        return ;
      }
      if(this.phone == ''){
        Toast({
          message: '',
          duration: 3000
        });
        return ;
      }
      if(this.phone.length !== 11){
        Toast({
          message: '11',
          duration: 3000
        });
        return ;
      }
      if(!this.selectedTime){
        Toast({
          message: '',
          duration: 3000
        });
        return ;
      }
      this.showCheckCode = true;
    },
    getCheckCode() {
      if(this.isCD){
        return ;
      }
      var time = 60;
      var timer = setInterval(() => {
        this.isCD = true;
        this.sendCodeCount++;
        time--;
        this.codeText = time + '';
        if(time < 0){
          this.isCD = false;
          clearInterval(timer);
          this.codeText = '';
        }
      }, 1000);

      this.sendReq('customer', {
        "v": "2.3",
        "method": "sendCheckcode",
        "params": {
          "devId": "5555998cccf2492db015c442f087f00a",
          "jpushId": "",
          "mobile" : this.phone
        }
      }).then((res) => {
        if(res.data.code == 0){
          Toast({
            message: '',
            duration: 3000
          });
        } else {
          Toast({
            message: res.data.message,
            duration: 3000
          });
        }
      }).catch((err) => { console.log(err) })
    },
    sendOrder() {
      if(!this.captcha){
        Toast({
          message: '',
          duration: 3000
        });
        return ;
      }
      if(this.captcha.length !== 6){
        Toast({
          message: '6',
          duration: 3000
        });
        return ;
      }
      let year = Number(this.selectedTime.split('/')[0]);
      let month = Number(this.selectedTime.split('/')[1]);
      let date = Number(this.selectedTime.split('/')[2].split('(')[0]);
      let hour = Number(this.selectedTime.split(' ')[1].split(':')[0]);
      let minute = Number(this.selectedTime.split(' ')[1].split(':')[1]);
      this.sendReq('customer', {
        "v": "2.3",
        "method": "tenantBooking",
        "params": { 
          "devId": "5555998cccf2492db015c442f087f00a",
          "jpushId": "",
          "housingType": this.type,
          "positionId": this.roomId,
          "name" : this.name,
          "phone" : this.phone,
          "bookingTime" : new Date(year, month-1, date, hour, minute, 0).getTime(),
          "remark" : this.remark,
          "vcode" : this.captcha,
          "isIntelligent": false
        }
      }).then((res) => {
        if(res.data.code == 0){
          this.orderSuccess = true;
          this.$modal.show('dialog', {
            title: '',
            text: ' <span style="color:#4680ff">' + this.selectedTime + '</span> ',
            buttons: [
              { 
                title: '',
                handler: () => { this.$modal.hide('dialog') }
              }
           ]
          })
        } else {
          this.$modal.show('dialog', {
            title: '',
            text: res.data.message || '',
            buttons: [
              { 
                title: '',
                handler: () => { this.$modal.hide('dialog') }
              }
           ]
          })
        }
      }).catch((err) => { console.log(err) })
    },
    jumpUrl() {
      if(this.orderSuccess){
        window.location.href = 'https://www.mdguanjia.com/waptest/houseInfo/appdownload/index.html';
      }
    }
  },
  watch: {
    remark(val) {
      if(val.length > 50){
        Toast({
          message: '50',
          duration: 3000
        });
        this.remark = val.substr(0, 10);
      }
    }
  },
  mounted() {
    this.setTimeList();
  }
}
</script>
<style lang="scss">
  .booking-container {
    .booking-block {
      margin-bottom: 0.4rem;
    }
    .booking-box {
      padding: 0.4rem;
      background-color: #fff;
    }
    .tips {
      padding-left: 0.4rem;
      color: #999;
    }
    .room-box {
      display: flex;
      justify-content: center;
      .room-pic {
        width: 3.36rem;
        height: 2.533333rem;
        img {
          width: 100%;
          height: 100%;
        }
      }
      .room-info {
        width: 5.84rem;
        padding-left: .346667rem;
        & > div {
          overflow: hidden;
          text-overflow: ellipsis;
          white-space: nowrap;
        }
        .room-name {
          font-size: .4rem;
          margin-bottom: .213333rem;
        }
        .room-layout {
          color: #999;
          margin-bottom: .373333rem;
        }
        .room-price {
          color: #ee7500;
          margin-bottom: .426667rem;
          font-size: .426667rem;
        }
      }
    }
    .input-group {
      position: relative;
      display: flex;
      justify-content: space-between;
      align-items: center;
      &:first-child {
        border-bottom: 1px solid #f5f5f5;
      }
      input, label {
        font-size: 0.373333rem;
      }
      input {
        width: 7.2rem;
      }
      label {
        flex-grow: 1;
      }
      .icon {
        position: absolute;
        top: 0.48rem;
        right: 0.4rem;
        width: 0.4rem;
        height: 0.4rem;
        background-image: url(../assets/images/icon-r.png);
        background-size: cover;
      }
    }
    .remarks {
      label {
        display: block;
        font-size: 0.373333rem;
        margin-bottom: 0.4rem;
      }
      textarea {
        width: 100%;
        font-size: 0.32rem;
      }
    }
    .order-btn {
      margin: 0.8rem 0.4rem 0;
      .btn {
        padding: 0.4rem 0;
        width: 100%;
        font-size: 0.373333rem;
        line-height: 1;
        border-radius: 1.173333rem;
      }
    }
    .order-msg {
      .v--modal {
        padding-top: 0;
        border-radius: 3px;
        .vue-dialog-button {
          color: #4680ff;
        }
      }
    }
  }
  .mj-picker {
    width: 100%;
    .picker-toolbar {
      height: 36px;
    }
    .mj-picker-head {
      line-height: 36px;
      font-size: 16px;
      text-align: center;
      padding: 0 0.4rem;
      span {
        font-size: 12px;
        color: #0575f2;
      }
      .fl {
        float: left;
      }
      .fr {
        float: right;
      }
    }
    .picker-slot {
      font-size: 0.373333rem;
    }
  }
  .checkCode-modal {
    text-align: center;
    background-color: #f8f8f8;
    border-radius: 10px;
    .checkCode-container {
      padding: 0.4rem;
    }
    .head {
      font-size: 0.426667rem;
    }
    .head, .phone, .mint-field {
      margin-bottom: 0.32rem;
    }
    .mint-field {
      width: 6.4rem;
      padding: 0.16rem 0;
      min-height: auto;
      .mint-cell-wrapper {
        padding: 0;
        flex-direction: row-reverse;
        font-size: 0.373333rem;
        background: none;
      }
      .mint-cell-value {
        padding: 0 0.24rem;
      }
      .mint-field-other {
        margin-left: 0.16rem;
        padding-left: 0.24rem;
        border-left: 1px solid #ddd;
      }
    }
    .tips {
      margin-bottom: 0.24rem;
    }
    .tips, .contract {
      font-size: 0.32rem;
    }
    .active {
      color: #4680ff;
    }
    .checkCode-confirm {
      padding: 0.4rem 0;
      border-top: 1px solid #ddd;
      font-size: 0.426667rem;
    }
  }
  .contract-modal {
    width: 100%;
    height: 100%;
    .head {
      position: absolute;
      top: 0;
      left: 0;
      z-index: 50;
      width: 100%;
      padding: 0.4rem;
      text-align: center;
      border-bottom: 1px solid #ddd;
      background-color: #fff;
      font-size: 0.426667rem;
      .close-btn {
        position: absolute;
        top: 0.2rem;
        right: 0.2rem;
        width: 0.8rem;
        height: 0.8rem;
        background-image: url(../assets/images/close-btn.png);
        background-size: cover;
      }
    }
    .contract-container {
      width: 100%;
      height: 100%;
      padding: 0.4rem;
      padding-top: 1.6rem;
      line-height: 1.5;
      overflow-x: hidden;
      overflow-y: scroll;
    }
    p {
      text-indent: 2em;
    }
  }

</style>


